@use '../helpers'as h;
@use '../components';

.l-header {
  position: relative;
  z-index: 9;
}

.menu-line {
  height: 108px;
  display: flex;
  justify-content: space-between;
  // display: grid;
  // grid-template-columns: repeat(3, 1fr);
  text-align: center;
  align-items: center;
  padding: 0 45px 0px 50px;
  background: #fff;
}

.open-menu {
  height: 22px;
  width: 31px;
  cursor: pointer;
  margin-right: 40px;

  &:hover {
    span {
      background-color: #000;
    }
  }

  span {
    transition: all ease 0.3s;
    display: block;
    width: 100%;
    height: 2px;
    // border-bottom: 2px solid #727272;        
    background-color: #727272;
    border-top-right-radius: 1px;
    border-bottom-right-radius: 1px;
    border-bottom-left-radius: 1px;
    border-top-left-radius: 1px;

    &:not(:last-child) {
      margin-bottom: 8px;
    }
  }
}

.logo-link {
  width: 55px;
  height: 44px;
  margin-right: 53px;

  img {
    width: 100%
  }
}

.brands-line {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.brands-line-name {
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 32px;
  display: flex;
  align-items: center;
  color: #0B0B0B;
  margin-right: 34px;

}

.wrp-brand {
  height: 73px;
  margin-right: 42px;
  transition: 0.3s ease all;
  padding: 10px 15px;
  border-radius: 10px;

  img {
    width: 100%;
  }

  &:hover {
    background-color: red;

    svg path {
      fill: #fff;
    }
  }

  &.dark-back:hover {
    background-color: rgb(0, 0, 0);

    svg path {
      fill: #fff;
    }
  }
}


.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 23;
  top: 0;
  opacity: 0;
  left: -100%;
  background-color: #fff;
  transition: 0.3s;
  width: 369px;
  padding-left: 49px;
  padding-top: 96px;

  ul {
    // padding-left: 32px;
    margin-bottom: 67px;
  }
}

.menu-back-call {

  // padding-left: 32px;
  .clb-name {
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 31px;
    color: #0B0B0B;
    margin-bottom: 15px;
    ;
  }
}

.sidenav .list a {
  padding: 8px 8px 12px 0px;
  text-decoration: none;
  font-size: 18px;
  color: rgb(54, 54, 54);
  display: block;
  transition: 0.4s;
  font-weight: 400;
  font-size: 25px;
  line-height: 30px;
  color: #0B0B0B;
  transition: all ease 0.3s;
}

.sidenav .list a:hover {
  color: #1A984F;
}

.sidenav .closebtn {
  position: absolute;
  right: 17px;
  top: 16px;
  font-size: 36px;
  margin-left: 50px;
}

.clb-link {
  display: flex;
  font-style: normal;
  margin-bottom: 28px;

  span {
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: #0B0B0B;
  }

  &:hover {
    span {
      color: h.$second-accent;
    }
  }

  svg {
    max-width: 22px;
    margin-right: 15px;

    path {
      // fill: red;
    }
  }

}

.menu-btn {
  padding: 25px 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  transition: all ease 0.3s;

  svg {
    width: 22px;
    margin-right: 15px;
    ;

    path {
      // fill: rgba(223, 27, 5, 1);

    }
  }

}





@media screen and (max-height: 450px) {
  .sidenav {
    padding-top: 15px;
  }

  .sidenav .list a {
    font-size: 18px;
  }
}


.main-menu,
.right-menu-info,
.left-menu {
  display: flex;
  align-items: center;
}

.right-menu-info {
  justify-content: flex-end;
}


.main-menu {
  &-item {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 800;
    font-size: 14px;
    line-height: 28px;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    color: #0B0B0B;
    cursor: pointer;

    &:hover {
      color: #000;
      cursor: default;
    }

    &:not(:last-child) {
      padding-right: 42px;
      ;

    }

  }
}


.search-form {
  position: relative;
  margin-right: 37px;
  top: 6px;
}


input[type=text] {
  width: 130px;
  -webkit-transition: width 0.4s ease-in-out;
  transition: width 0.4s ease-in-out;
}

/* When the input field gets focus, change its width to 100% */
input[type=text]:focus {
  width: 100%;
}

form.search-bar {
  position: relative;
  // transform: translate(-50%,-50%);
  transition: all 1s;
  width: 50px;
  height: 42px;
  // background: rgb(167, 165, 165);
  box-sizing: border-box;
  border-radius: 25px;
  // border: 4px solid rgb(5, 5, 5);
  padding: 5px;
  margin-right: 30px;
  ;

}

form.search-bar input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  ;
  height: 42.5px;
  line-height: 30px;
  outline: 0;
  border: 0;
  display: none;
  font-size: 1em;
  border-radius: 0px;
  padding: 0 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  ;
}

.fa {
  box-sizing: border-box;
  padding: 10px;
  width: 42.5px;
  height: 42.5px;
  position: absolute;
  top: 0px;
  right: 0;
  border-radius: 50%;
  color: #07051a;
  text-align: center;
  font-size: 1.2em;
  transition: all 1s;
  // background-image: url('../img/icons/search.svg');
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 50%;
  background-size: 21px;

  .search-icon {
    fill: #727272;
  }

}

form.search-bar:hover,
form.search-bar:valid {
  width: 200px;
  cursor: pointer;
}

form.search-bar:hover input,
form.search-bar:valid input {
  display: block;
}

form.search-bar:hover .fa,
form.search-bar:valid .fa {
  background-color: h.$accent-color;
  color: white;
  border-radius: 10px;

  .search-icon {
    fill: #fff;
  }
}


form.search-bar a {
  bottom: 0;
  color: #027cc2;
  display: none;
  font-size: 13px;
  left: -90px;
  position: absolute;
  right: auto;
  text-align: center;
  top: 13px;
  width: 100%;
}

form.search-bar:valid a {
  display: block;
}







